import React, { Component } from 'react';
import { log } from 'ruucm-util';
// const {auth, ref} = require('../../auth')
import {pushToFirebase, login} from '../../auth'

class ProgramSubmit extends Component {
	constructor(props) {
    super(props);
    this.state = {
      isGoing: true,
      numberOfGuests: 2
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  handleInputChange(event) {
    const target = event.target;
    const value = target.type === 'checkbox' ? target.checked : target.value;
    const name = target.name;

    log('event', event);
    log('value', value);

    this.setState({
      [name]: value
    });
  }
  handleSubmit(event) {
    // alert('A name was submitted: ' + this.state.numberOfGuests);
    event.preventDefault();
    const { isGoing, numberOfGuests } = this.state;
    pushToFirebase(this.state.name, this.state.email, this.state.age, this.state.why);
  }
  componentDidMount() {
    
  }
  render() {
    return (
      <form onSubmit={this.handleSubmit}>
        <label>
          
          <input
            name="name"
            type="text"
            checked={this.state.name}
            onChange={this.handleInputChange} />
        </label>
        <br />
        <label>
           (: abc@abc.com)
          <input
            name="email"
            type="email"
            value={this.state.email}
            onChange={this.handleInputChange} />
        </label>
        <label>
           !
          <input
            name="age"
            type="number"
            value={this.state.age}
            onChange={this.handleInputChange} />
        </label>
        <label>
              ,   
          <textarea
            name="why"
            type="text"
            value={this.state.why}
            onChange={this.handleInputChange} />
        </label>
        <input type="submit" value="Submit" />
      </form>
    );
  }
}

export default ProgramSubmit;




